body {
  background-color: #000;
}
#landing-header {
    z-index: 1;
    position: relative;
    text-align: center;
    padding-top: 40vh;
}

#landing-header h1 {
  color: #fff;
}

.slideshow {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  list-style: none;
  margin: 0;
  padding: 0;
}

.slideshow li {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0;
  z-index: 0;
  -webkit-animation: imageAnimation 50s linear infinite;
          animation: imageAnimation 50s linear infinite;
}


.slideshow li:nth-child(1) {
  background-image: url(https://i.imgur.com/2QoQ2C5.jpg)
}
.slideshow li:nth-child(2) {
  background-image: url(https://i.imgur.com/0YMyLQm.jpg);
  -webkit-animation-delay: 10s;
          animation-delay: 10s;
}
.slideshow li:nth-child(3) {
  background-image: url(https://i.imgur.com/3b4r0pH.jpg);
  -webkit-animation-delay: 20s;
          animation-delay: 20s;
}
.slideshow li:nth-child(4) {
  background-image: url(https://ssl.c.photoshelter.com/img-get/I0000SfOWZCr1Hnw/s/700/700/Mule-on-the-walk-between-Oukaimeden-and-Tacheddirt-High-Atlas-Mountains-Morocco-North-Africa-Africa-dsc-8384.jpg);
  -webkit-animation-delay: 30s;
          animation-delay: 30s;
}
.slideshow li:nth-child(5) {
  background-image: url(https://i.imgur.com/2XCp3RA.jpg);
  -webkit-animation-delay: 40s;
          animation-delay: 40s;
}

@-webkit-keyframes imageAnimation {  
  0% {
    opacity: 0;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  10% {
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  20% {
    opacity: 1
  }
  30% {
    opacity: 0
  }
}

@keyframes imageAnimation {  
  0% {
    opacity: 0;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  10% {
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  20% {
    opacity: 1
  }
  30% {
    opacity: 0
  }
}

